/* Date Picker Styling */

.nav-button {
  background: white;
  border-radius: 50%;
  color: black;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

.nav-button:disabled {
  opacity: 0.3;
  cursor: default;
}

.react-datepicker__day--today {
  font-weight: bold;
  color: deepskyblue;
}

.react-datepicker__day--keyboard-selected {
  background-color: #e0e0e0;
}

.react-datepicker__footer {
  text-align: center;
  padding: 10px;
}

.react-datepicker__footer button {
  background: none;
  border: none;
  cursor: pointer;
  color: green;
}

/* Show only current month dates (hide others) */
.react-datepicker__day--outside-month {
  visibility: hidden;
}

/* All current month dates in blue */
.react-datepicker__day {
  color: #3498db;
}

/* Today (current day) in black */
.react-datepicker__day--today {
  color: black !important;
}

/* Selected date in red */
.react-datepicker__day--selected {
  background-color: transparent !important;
  color: red !important;
  font-weight: bold;
  background-color: gold !important;
}

/* Remove gray hover background */
.react-datepicker__day:hover {
  background-color: #e6f7ff;
}

/* Optional: remove highlight from keyboard nav */
.react-datepicker__day--keyboard-selected {
  background-color: transparent !important;
}

